@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding:50px;
  background:url(/static/media/bg.340111c4.png) no-repeat center center;
  background-size:cover;
  font-family: 'DM Sans', sans-serif;
}


.box {
  position:relative;
  display:inline-block;
  background: #FFFFFF;  
  box-shadow: 0px 0px 16px rgba(58, 78, 88, 0.05);
  border-radius: 12px;
  padding:15px 30px;
  box-sizing:border-box;
  margin:25px 0px;
}

.box.full {
  width:100%;
}


.container {
  display:block;
  padding:15px 25px;
  max-width:1250px;
  margin:0px auto;
}

.meter {
  border-radius:10px;
  display:block;
  width:100%;
  height:23px;
  background:#f8f8f8;
  max-width:900px;
}

.meter > span {
  border-radius:10px;
  background: #0C3EE3;
  height:100%;
  display:block;
  width:auto;
}

.icon {
  max-width:50px;
  width:100%;
  padding:10px;
}

.flex {
  display:flex;
  align-items: center;
  flex-direction: row;

}

.flex>div:last-of-type {
  min-width:150px;
  color:#5E6D7C;
  font-size:16px;
  padding:10px;
  box-sizing:border-box;
}

.icon img {
  width:100%;
  max-width:40px;
  margin:0px auto;

}



.score {
  max-width:250px;
}

.score-circle {
  stroke: #0C3EE3;
  transition: stroke-dashoffset 1s ease-out;
}

.js-circle {
  stroke-linecap: round;

}

.score-empty {
  stroke: #f8f8f8;
}

.score-text {
  fill: #1e1935;
  font-size:60px;
  font-weight:bold;
}
.score-text.small {
  fill:#5E6D7C;
  font-size:25px;
}

p.big {
  font-size: 90px;
  font-weight: bold;
  margin:0px;
  position:absolute;
  top:50%;
  left:50%;
  padding-top:25px;
  transform:translate(-50%,-50%)
}

.tvl {
  margin-right:50px;
  width:100%;
  max-width:825px;
  height:337px;
}

@media only screen and (max-width: 1200px)  {
  body {
    padding:50px 25px;
  }
  p.big {
    font-size:50px;
  }
}

@media only screen and (max-width: 980px)  {
  .box {
    margin:15px 0px;
  }
  .flex {
    flex-wrap: wrap;
  }
  .tvl {
    height:auto;
  }
  p.big {
    padding-top:0px;
    padding-bottom:10px;
    width:100%;
    text-align:center;
    position:relative;
    top:auto;
    left:auto;
    transform:none;
  }
  .flex > div {
    
      flex:1 0 100%;
      max-width:100% !important;
  }
}


@media only screen and (max-width: 765px)  { 
  body {
    padding:25px 15px;
  }
  .container {
    padding:0px;
  }
  p.big {
    font-size:35px;
  }
  .full > div {
    text-align:center;
  }
  .full > div .flex {
    position:relative;
    margin-bottom:25px;
  }
  .icon {
    padding:0px;
    display:block;
    text-align:left;
  }
  .meter + div {
    position: absolute;
    right: 0px;
    top: 0px;
    padding-right:0px !important;
    text-align:right;
  }

}
